.menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;
    position: relative;
    overflow: hidden;

    &:hover {
      cursor: pointer;
    
      .menu-item__image {
        transform: scale(1.1);
      }
    
      .menu-item__content {
        opacity: 0.9;
      }
    }
  
    &:first-child {
      margin-right: 7.5px;
    }
  
    &:last-child {
      margin-left: 7.5px;
    }

    &.large {
      height: 300px;
    }
  
    &__content {
      height: 90px;
      padding: 0 25px;
      line-height: 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
      background-color: white;
      opacity: .7;
      position: absolute;
      transition: opacity .3s
    }

    &__content h1 {
      margin: 0;
    }

    &__title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
    }

    &__subtitle {
      font-weight: lighter;
      font-size: 16px;
    }

    &__image {
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
  }